.header{
  background-image: url('../images/abstract-background-with-red-lines\ 1.jpg');
  background-color: rgb(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-size:cover;
  
  height: 215px;
  @media screen and (min-width: 768px) {
    
   
}
}
.header-film__container{ 
  flex-wrap: nowrap;
  justify-content: center;
  padding-right: 32px;
  padding-left: 32px;
  margin-left: auto;
  margin-right: auto;
}

.header-film__main-wraper{
  padding-top: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-film__logo-link{
  display: flex;
  align-items:center;
  justify-content: center;
  flex-direction: row-reverse;
}
.header-film__logo-name{
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.16667;
  display: flex;
  align-items: center;
  text-align: center;
  
  color: #FFFFFF;
}
.header-film__logo {
  cursor: pointer;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  
  color: rgba(255, 255, 255, 1);
  font-weight: 500;
  font-size: 30px;
  line-height: 1.16;
}
.header-film__logo-svg{
  margin-right: 8px;
}
  // navigation
.header__navigation{
  
display: flex;
align-items: center;
}

.header__navigation-button{
    display: flex;
    justify-content: space-between;
    
  }
  .header__navigation-item{
      margin-right: 40px;
    }
    .header__navigation-item:nth-child(2){
      margin-right: 0;
    }
 .header__navigation-link{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;

    display: flex;
    text-transform: uppercase;
    color: #FFFFFF;
    }

.main-current{
  position: relative;

  height: 100%;

  transition-property: color;
  transition-duration: 250ms;

  
  &::after {
      content: ' ';
      position: absolute;
      bottom: -1px;
      display: block;
      width: 100%;
      height: 3px;
      background-color:#FF001B;
  }
}



/// search div
.header-film__search-wraper{
  margin: auto;
  margin-top: 40px;
  position: relative;
  width: 380px
}
.header__form{
   display: flex;
   position: relative;
   margin: auto;
  }

////////input form
.header-film__input {
  position: relative;
  width: 100%;
  border: none;
  border-bottom: 0.5px solid #fff;
  padding: 3px 16px 3px 1px;
  outline: none;
  background-color: transparent;
  color: #FFFFFF;
}

