.header__search-icon{
  position: absolute;
  right: 0px;
}

.header-film__warning-msg{
   display: flex;
   justify-content: center;
   text-align: center;
   align-items: center;
   margin-top: 16px;
   margin-bottom: 45px;
   
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  color: #FF001B;
}

.header__form-search{
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  padding: 0;
  
}
.header__form-search:hover,
.header__form-search:focus{
  transform: "scale(1.25)";
}
